<template lang="pug">
  .item(:class='{ needFold, folded: needFold && folded }')
    .title
      span.name {{ data.username }}
      |  · 
      span.time {{ data.createTime }}
    p.content(ref='content')
      span(ref='inner') {{ data.content }}
      .fold-handle(v-if='needFold && !folded')
        span(@click='handleFold') 🍭 查看全部
</template>

<script>
export default {
  props: ['data'],
  mounted() {
    this.calc()
  },
  data() {
    return {
      folded: false,
      needFold: false
    }
  },
  watch: {
    data() {
      this.calc()
    }
  },
  methods: {
    calc() {
      this.$nextTick(() => {
        let wrap = this.$refs.content.offsetHeight
        let inner = this.$refs.inner.offsetHeight
        this.needFold = inner > wrap
        this.folded = false
      })
    },
    handleFold() {
      this.folded = true
    }
  }
}
</script>
