<template lang="pug">
  .words-list
    .topbar
      span 当前 {{ data.length }} 条
      button(:disabled='loading', @click='handleRefresh')
        span(v-if='loading && !loaded') 加载中
        span(v-if='loading && loaded') 刷新中
        span(v-if='!loading') 刷新
    template(v-show='loaded && !ban', v-for='item in data')
      list-item(:data='item')
    .content-hint(v-show='loaded && ban') {{ ban }} 😑
    .content-hint(v-show='!loaded') 加载中... 😏
</template>

<script>
import ListItem from './components/ListItem'

export default {
  name: 'WordsList',
  props: ['data', 'loaded', 'ban', 'loading'],
  methods: {
    handleRefresh() {
      this.$emit('need-refresh')
    }
  },
  components: {
    ListItem
  }
}
</script>
