import request from '@/lib/el-request'
import qs from 'qs'

export function fetchWordsList() {
  return request({
    method: 'get',
    url: '/getWords'
  })
}

export function submitWord(text) {
  return request({
    method: 'post',
    url: '/leaveWord',
    data: qs.stringify({ content: text })
  })
}
