<template lang="pug">
  #app
    h2.super-title 留言板板
    .words-edit
      .hinter 🙉 你对在座的各位有什么要说的？
      textarea(ref='input', :disabled='sending', maxLength='1024', v-model='text', rows='3', placeholder='富强、民主、文明、和谐\r\n自由、平等、公正、法治\r\n爱国、敬业、诚信、友善')
      .bottom
        button(:disabled='!text.length || sending', @click='handleSubmit')
          span(v-if='sending') SENDING...
          span(v-else) SUBMIT ({{ text.length }}/1024)
    .words-main
      words-list(:data='list', @need-refresh='fetchWordsList', :loaded='loaded', :ban='ban', :loading='loading')
</template>

<script>
import { fetchWordsList, submitWord } from '@/api/app'

export default {
  name: 'app',
  mounted() {
    this.fetchWordsList()
    this.$refs.input.focus()
  },
  data() {
    return {
      sending: false,
      text: '',
      list: [],
      ban: null,
      loaded: false,
      loading: false
    }
  },
  methods: {
    async fetchWordsList() {
      try {
        this.loading = true
        let res = await fetchWordsList()
        this.loading = false
        this.list = res
        this.loaded = true
        this.ban = null
      } catch (err) {
        this.loaded = true
        this.loading = false
        this.ban = `获取数据失败 (${err.response.status})`
      }
    },
    async handleSubmit() {
      try {
        this.sending = true
        await submitWord(this.text.slice(0, 1024))
        this.fetchWordsList()
        this.text = ''
        this.sending = false
      } catch (err) {
        this.sending = false
        alert(`留言失败 (${err.response.status})`)
      }
    }
  }
}
</script>
